<template>
    <div class="main">
        <van-nav-bar
            :title="list.title"
            left-arrow
            @click-left="$router.go(-1)"
            />
        <!-- <h2 @click="$router.go(-1)">{{list.title}}</h2> -->
        <div class="box">
            <van-pull-refresh class="aa" v-model="isLoading" @refresh="onRefresh">
                <div class="top">
                    <img :src="list.img" alt="">
                </div>
                <div class="商品详情">
                    {{ list.info }}
                </div>
                <div>
                    剩余数量<span>{{list.num.number}}</span>
                </div>
            </van-pull-refresh>
            
        </div>
        <van-button class="fix" type="primary" @click="addCar(list)" size="large" :disabled = "listLength == 0"> 加入购物车( {{userCar}} )    </van-button>
        <!-- <button class="fot" @click="addCar(list)" :disabled = "listLength == 0">
            加入购物车( {{userCar}} )    
        </button> -->
    </div>
</template>
<script>
import { mapState , mapActions } from "vuex";
var that ;
export default {
    data () {
        return {
            img:"",
            info:"",
            title:"",
            num:"",
            count: 0,
      isLoading: false
        }
    },
    computed: {
        ...mapState({
            list: state =>{
                return state.car.list.find(ele =>{
                    return ele.id == that.$route.params.id
                })
            },
            listLength:state =>{
                var a = state.car.list.find(ele =>{
                    return ele.id == that.$route.params.id
                }).num.number;
                return a
            },
            userCar:state => state.car.userCar.reduce(function(olds,news){
                return parseInt(olds) + parseInt(news.num)
            },0)
        })
    },
    methods: {
        onRefresh() {
            setTimeout(() => {
                this.$toast('刷新成功');
                this.isLoading = false;
                this.count++;
            }, 500);
        },
        addCar(val){
            var num = 1;
            var id = this.$route.params.id;
            this.$store.commit("car/addCar", { id , num , val});
        }
    },
    created () {
        that = this;
        //console.log(this.$store.getters["car/getInfo"](this.$route.params.id));
        // var info = this.$store.getters["car/getInfo"](this.$route.params.id)
        // this.img = info.img;
        // this.info = info.info;
        // this.title = info.title;
        // this.num = info.num.number;
    }
}
</script>
<style lang="scss" scoped>
.fix{
    position: fixed;
    bottom: 0;
    left: 0;
}
h2{
    width: 90%;
    margin: 0 auto;
    padding: 10px 0;
    background: url(~@/assets/back.png) no-repeat left center;
}
.main{
    height: 100%;
    display: flex;
    flex-direction: column;
    .box{
        flex: 1;
        width: 90%;
        margin: 0 auto;
        .aa{
            height: 100%;
        }
        span{
            color: red;
            font-weight: bold;
        }
    }
    .fot{
        padding: 20px 0;
        
        outline: none;
        border: none;
        border-top: 1px solid #ccc;
        background: transparent;
    }
}
</style>
