<template>
    <div>
        <div class="login" v-if="userInfo.username == ''">
            <van-cell-group>
                <van-field
                    v-model="username" 
                    required
                    clearable
                    label="用户名"
                    right-icon="question-o"
                    placeholder="请输入用户名"
                    @click-right-icon="$toast('question')"
                />

                <van-field
                    v-model="userpass"
                    type="password"
                    label="密码"
                    placeholder="请输入密码"
                    required
                />
                </van-cell-group>
            <!-- <label for="">请输入用户名<input type="text" v-model="username" placeholder="请输入用户名"></label>
            <hr>
            <label for="">请输入密码<input type="password" v-model="userpass" placeholder="请输入密码"></label> -->
            <!-- <button @click="logins">登陆</button> -->
            <van-button class="btns" size="large" round  @click="logins">登陆</van-button>
        </div>
        <div class="logout" v-else >
            <van-cell-group>
                <van-cell title="用户名" :value="userInfo.username" />
                <van-cell title="用户密码" :value="userInfo.userpass "/>
            </van-cell-group>
            <!-- <div>
                用户名 {{ userInfo.username }}
            </div>
            <div>
                用户密码 {{ userInfo.userpass }}
            </div> -->
            <van-button class="btns" size="large" round  @click="logout">退出登陆</van-button>
            <!-- <button @click="logout">退出登陆</button> -->
        </div>
    </div>
</template>
<script>
import { mapActions , mapState} from "vuex"
export default {
    data () {
        return {
            username : "",
            userpass : ""
        }
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.userInfo
        })
    },
    methods: {
        ...mapActions({
            login:"user/login"
        }),
        logins(){
            var list = this.$router.options.routes;
            var ele = list.find(ele => ele.name == "car");
            ele.meta.isLogin = true;
            var username = this.username,userpass = this.userpass;
            this.login({username , userpass});
            console.log(ele,this.$router.options.routes)
        },
        logout(){
            this.userInfo.username = "";
            this.userInfo.userpass = "";
            var list = this.$router.options.routes;
            var ele = list.find(ele => ele.name == "car");
            ele.meta.isLogin = false;
            localStorage.clear();
        }
    },
    created () {
    }
}
</script>
<style lang="scss">
.btns{
    margin: 0;
    margin-top: 20px!important;
}
</style>

