export default {
    state: { 
        userInfo:{
            username : localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")).user : "",
            userpass : localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")).pass : ""
        }
    },
    mutations: { 

    },
    actions: { 
        login({state},{ username , userpass }){
            console.log(username,userpass)
            state.userInfo.username = username
            state.userInfo.userpass = userpass;
            var obj = {
                user:username,
                pass:userpass
            }
            localStorage.setItem("user",JSON.stringify(obj))
        }
    },
    getters: {  },
    namespaced: true
}